@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Oswald'
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #6c757d10;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: 'Great Vibes', cursive;
  src: local('Great Vibes'), url(/portfolio/static/media/GreatVibes-Regular.c1c92d0c.ttf) format('truetype');
}
.App{display:flex;min-height:100vh;flex-direction:column;font-family:"Helvetica Neue", Helvetica, Arial,sans-serif;font-size:14px;line-height:1.42857143;text-shadow:0 1px 1px #fff}.App .dynamic-area{flex:1 1}p{line-height:"1.5rem"}a{text-decoration:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.site-content{text-align:justify;margin-left:5rem;margin-right:5rem}.content{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;margin-bottom:4rem}.content .content-left,.content .content-right{display:flex;flex-direction:column;width:32rem;padding-top:2rem}.content .content-left{margin-right:2.5rem;margin-left:2.5rem}.content .content-left .stylish-name{font-family:'Roboto', sans-serif;justify-content:center;margin:0 0 0 1.5rem;text-align:left;text-shadow:0 1px 1px #fff;line-height:2rem}.content .content-left .dp{padding:1rem 0 0 1rem;height:20rem;width:20rem}.content .content-right{margin-left:2.5rem;margin-right:2.5rem}@media screen and (max-width: 480px){.content-left,.content-right{width:20rem !important;margin-bottom:1rem;margin-left:1.5rem !important;margin-right:1.5rem !important}.site-content{margin-left:1rem;margin-right:1rem}.dp{padding:1rem 0 0 1rem !important}}@media (min-width: 768px) and (max-width: 991px){.content-left,.content-right{width:25rem !important}}@media (min-width: 992px) and (max-width: 1199px){.content-left,.content-right{width:25rem !important}}

.header-row{display:flex;flex-direction:row}.empty-space{flex:50% 1}.nav-bar{list-style-type:none;flex:50% 1;padding-top:4rem}.nav-bar .nav-mem{padding:0.4rem;margin-right:1rem;border-radius:0.2rem;text-decoration:none;color:black}.nav-bar .nav-mem:hover{color:#364962}.nav-bar .active-menu-item{padding:0.6em;margin-right:1rem;border-radius:0.2rem;text-decoration:none;color:black;background-color:#DCDCDC}@media screen and (max-width: 767px){.header-row{flex-direction:column;justify-content:center;text-align:center}.nav-bar{flex:100% 1}}@media (min-width: 768px) and (max-width: 991px){.header-row{flex-direction:column;justify-content:center;text-align:center}.nav-bar{flex:100% 1}}@media (min-width: 992px) and (max-width: 1199px){.header-row{flex-direction:column;justify-content:center;text-align:center}.nav-bar{flex:100% 1;-webkit-padding-start:0rem;padding-inline-start:0rem}}@media screen and (min-width: 1200px){.nav-bar{padding-left:6rem}}

.content-bottom{position:relative;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;height:2.5rem;bottom:1rem}

.site-content{text-align:justify;margin-left:1rem !important;margin-right:1rem !important}.box{display:flex;flex-direction:row;flex-wrap:wrap;margin-top:1rem}.box .box-left,.box .box-right{display:flex;flex-direction:column}.box .box-left{margin-left:3rem;width:20rem;margin-top:2rem}.box .box-left .buttons{margin-top:1rem;display:flex;flex-direction:row;justify-content:space-between}.box .box-left .buttons .boxleft1{width:40%}.box .box-left .buttons .boxleft2{width:40%}.box .box-right{margin-left:5rem;width:60rem;border-color:black;justify-content:center}.box .error{position:relative;-webkit-animation:shake .1s linear;animation:shake .1s linear;-webkit-animation-iteration-count:3;animation-iteration-count:3}@-webkit-keyframes shake{0%{left:-5px}100%{right:-5px}}@keyframes shake{0%{left:-5px}100%{right:-5px}}

.books-container{padding:2.5rem}.books-container .stylish-name{font-family:'Lato', sans-serif;text-align:left;text-shadow:0 1px 1px #fff;padding:2rem}.books-container li{line-height:1.5;font-family:Great Vibes;margin:0 0 0 3rem;text-align:left;text-shadow:0 1px 1px #fff}

.user-box{display:flex;flex-direction:row;flex-wrap:wrap;flex-direction:column;justify-content:center;align-items:center}

.publications{margin-left:5rem;margin-right:5rem;line-height:8px;padding-top:2rem}.publications .contribution-weight{font-weight:700}

.projects{margin-left:5rem;margin-right:5rem}.projects .projects-container{display:flex;flex-direction:row;flex-wrap:wrap}.projects .projects-container .project-left,.projects .projects-container .project-right{display:flex;flex-direction:column}.projects .projects-container .project-left .project-block,.projects .projects-container .project-right .project-block{text-align:left}

